
#app .tab {
  position: relative;
}
#app .tab:after {
  display: block;
  position: absolute;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  width: 100%;
  content: ' ';
  bottom: 0;
  left: 0;
}
#app .tab ul {
  padding: 0;
  margin: 0;
  display: -ms-flexbox;
  display: flex;
}
#app .tab ul .tab-item {
  -ms-flex: 1;
      flex: 1;
  height: 40px;
  line-height: 40px;
  list-style: none;
  text-align: center;
}
#app .tab ul .tab-item > a {
  display: block;
  color: #4d555d;
  font-size: 14px;
}
#app .tab ul .tab-item > a.active {
  color: #f01414;
}



