
.cartconcontrol {
  font-size: 0;
}
.cartconcontrol .cart-decrrease {
  display: inline-block;
  padding: 6px;
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
.cartconcontrol .cart-decrrease .inner {
  font-size: 24px;
  height: 24px;
  color: #00a0dc;
  transition: all 0.4s linear;
  transform: rotate(0);
  display: inline-block;
  line-height: 24px;
}
.cartconcontrol .cart-decrrease.move-enter-active,
.cartconcontrol .cart-decrrease.move-leave-active {
  transition: all 0.4s linear;
}
.cartconcontrol .cart-decrrease.move-enter,
.cartconcontrol .cart-decrrease.move-leave-active {
  opacity: 0;
  transform: translate3d(24px, 0, 0);
}
.cartconcontrol .cart-decrrease.move-enter .inner,
.cartconcontrol .cart-decrrease.move-leave-active .inner {
  transform: rotate(180deg);
}
.cartconcontrol .cart-count {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  padding-top: 6px;
  line-height: 24px;
  text-align: center;
  font-size: 10px;
  color: #93999f;
}
.cartconcontrol .cart-add {
  display: inline-block;
  color: #00a0dc;
  padding: 6px;
  font-size: 24px;
  line-height: 24px;
}



